	async onLoad(options) {
		var that = this;
		//接收传值,id里面放的是标题，因为测试数据并没写id 
		let id = options.id;
		if (id) {
			this.goodid = id;

		}
		that.getBestGoods(id)


		//规格 默认选中第一条
		this.specList.forEach(item => {
			for (let cItem of this.specChildList) {
				if (cItem.pid === item.id) {
					this.$set(cItem, 'selected', true);
					this.specSelected.push(cItem);
					break; //forEach不能使用break
				}
			}
		})
		// this.shareList = await this.$api.json('shareList');
		
		//记录的位置miaoshu
		// let view = uni.createSelectorQuery().select("#tuwenxiangqing");
		// view.boundingClientRect( res => {
		// 	this.tuwenTop = res.top;
		// }).exec()
		
		//记录图文详情的位置miaoshu
		let view = uni.createSelectorQuery().select("#tuwenxiangqing");
		view.boundingClientRect( res => {
			this.tuwenTop = res.top;
		}).exec()
		
		//记录评价的位置
		let viewPingjia = uni.createSelectorQuery().select("#pingjia");
		viewPingjia.boundingClientRect( res => {
			this.pingjiaTop = res.top;
		}).exec()
		
		this.$set(this.goodDetail.llGoodsSkuList[0], 'selected', true);
		
	},
	methods: {
		//规格弹窗开关
		toggleSpec() {
			if (this.specClass === 'show') {
				this.specClass = 'hide';
				setTimeout(() => {
					this.specClass = 'none';
				}, 250);
			} else if (this.specClass === 'none') {
				this.specClass = 'show';
			}
		},
		//选择规格
		selectSpec(index, pid) {
			console.log(index)
			console.log(pid)
			let list = this.goodDetail.llGoodsSkuList;
			console.log(list)
			
			list.forEach(item => {
			
				// if (item.id === pid) {
					this.$set(item, 'selected', false);
				// }
			})

			this.$set(list[index], 'selected', true);
			
			//存储已选择
			/**
			 * 修复选择规格存储错误
			 * 将这几行代码替换即可
			 * 选择的规格存放在specSelected中
			 */
			this.specSelected = [];
			list.forEach(item => {
				if (item.selected === true) {
					this.specSelected.push(item);
				}
			})

		},
		//分享
		share() {
			this.$refs.share.toggleMask();
		},
		//收藏
