		},
		buy() {
			uni.navigateTo({
				url: `/pages/order/createOrder`
			})
		},

		getBestGoods: function(id) {
			var that = this;
			that.$api.getBestGoods({
				id: id
			}).then(res => {
				console.log(res)
				that.goodDetail = res.data;
				that.imgList.push(res.data.img)
			})
		},

		cart: function() {
